Contains:
*General Posts and Pages Layout
*Social Media 
*/

/* General Posts and Pages Layout */

.site-content {
  display: flex;
  width: $width;
  margin: auto;
  padding: 3rem 0;

  .content-area {
    width: 73%;
    margin: auto;
    padding-top: 5%;
    padding-right: 5%;

    .entry-header {
      position: relative;
      overflow: hidden;

      img {
        width: 100%;
        height: 100%;
      }

      .entry-title {
        position: absolute;
        top: 20px;
        left: 0;
        background: #248a83;
        padding: 10px;
        color: white;
        text-transform: uppercase;
        font-size: 2rem;
        & a {
          color: white;
          text-decoration: none;
          &:hover {
            color: #f0e4d1;
          }
        }
      }
      .entry-meta {
        position: absolute;
        bottom: 7px;
        right: 0;
        color: white;
        background: #248a83;
        padding: 5px;
        text-transform: uppercase;
      }
      .entry-content {
        width: 100%;
        padding-right: 5%;
        padding-top: 5%;
      }
    }
  }

  .widget-area {
    width: 27%;
    border-left: 1px solid #e1e1e1;
    padding: 1rem;
  }
}
.home,
.page-about,
.single-adventures {
  .site-content {
    display: block;
    width: 100%;
    margin: auto;
    padding-top: 0;

    .content-area {
      width: 100%;
      padding: 0;
      .container-header {
        display: flex;
        justify-content: center;
        text-transform: uppercase;
        font-size: 2.4rem;
        font-weight: 400;
        letter-spacing: 0.25rem;
      }
    }
  }
}

/* Social Media */
.social-media {
  display: flex;
  width: 60%;
  .btn-white {
    margin-right: 0.25rem;
    display: flex;
    justify-self: flex-start;
    align-items: center;
    .fab {
      padding-right: 0.25rem;
    }
  }
}
