/*Contains banner stylistics for .home, .page-about, .single-adventures*/

.home,
.page-about,
.single-adventures {
  width: 100%;
  .site-header {
    position: relative;
    width: 100%;
  }
  .site-content {
    .hero-banner {
      position: relative;
      height: 100vh;
      display: block;
      .wp-post-image {
        object-fit: cover;
        width: 100%;
        height: 100%;
        background-color: linear-gradient(
          180deg,
          rgba(0, 0, 0, 0.5) 0,
          rgba(0, 0, 0, 0.5)
        );
      }

      &-fp {
        position: relative;
        .wp-post-image {
          object-position: center top;
        }
        .logo-full {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }
      }

      &-about {
        .wp-post-image {
          object-position: center bottom;
        }
      }
      .hero-banner-title {
        position: absolute;
        z-index: 1;
        top: 40%;
        left: 50%;
        transform: translate(-50%, -50%);
        color: white;
        text-transform: uppercase;
        font-size: 3rem;
      }
    }
  }
}

.tent-logo {
  display: none;
}

.sticky .tent-green,
.tent-green.show-green,
.tent-white.show-white {
  display: block;
}

.sticky .tent-white {
  display: none;
}
